<script>
import qs from "qs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      datalist: {},
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      answerItems: [
        { score: 0, text: "幾乎沒有(0)" },
        { score: 1, text: "偶爾會有(1)" },
        { score: 2, text: "時常會有(2)" },
        { score: 3, text: "經常總是(3)" }
      ],
      loading:false,
    };
  },
  props: ["edititem", "viewData"],

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function() {
      this.dialog = true;
      this.editProcess();
    });
  },

  model: {
    prop: "edititem"
    // event: "parentData"
  },

  watch: {
    edititem: [
      {
        handler: "editProcess"
      }
    ]
  },

  computed: {
    total() {
      // console.log('run total')
      var total = 0;
      for (var i in this.datalist) {
        //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
        // console.log(this.datalist[i]);
        if (this.datalist[i] != null) {
          if (this.datalist[i].score != undefined) {
            total = total + this.datalist[i].score;
          }
        }
      }
      return total;
    }
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      // this.list = {};

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值
      this.list = { ...this.viewData[0] };
      this.list.snkey = "";
      this.list.date = "";
      if (this.list.datalist != undefined) {
        this.datalist = { ...this.list.datalist };
      }
      this.list.edit_man = "";
    },
    editProcess() {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = this.edititem; //將頁面資料與父項的資料連結
      this.list = { ...this.edititem }; //複制一份實際修改資料內容
      this.datalist = this.list.datalist;
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list.u_snkey = this.$store.state.uData.snkey;
        this.datalist["total"] = this.total;
        this.list.datalist = JSON.stringify(this.datalist);
        // this.list.total = this.total;
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.loading = true;
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/melancholy",
            qs.stringify(this.list)
          )
          .then(rs => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.datalist["total"] = this.total;
        this.list.datalist = JSON.stringify(this.datalist);
        // this.list.total = this.total;
        this.list["edit_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/melancholy",
            qs.stringify(this.list)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.oldList["datalist"] = this.datalist;
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    }
  }
};
</script>

